import React from 'react';
import { eye } from 'react-icons-kit/fa/eye';
import { send } from 'react-icons-kit/fa/send';
import { battery } from 'react-icons-kit/fa/battery';
import { lock } from 'react-icons-kit/fa/lock';


import { Icon } from 'react-icons-kit';

import HorizontalTimelineContent from './HorizontalTimelineContent';
import GameInfo from './content';

// Directly importing the minified bootstrap css to avoid all the painful
// steps it will take otherwise to get it to work.
// import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';

const card = (value, description, iconType) => {
  return (
      <div className="col-xs-12 col-sm-6 col-md-3">
          <div className="fun-facts">
              <div className="item">
                  {iconType === 'eye' &&  <span className="pe-7s-medal"/>}
                  {iconType === 'send' && <span className="pe-7s-cup"/>}
                  {iconType === 'battery' && <span className="pe-7s-stopwatch"/>}
                  {iconType === 'lock' && <span className="pe-7s-wristwatch"/>}
                  <h4>{value}</h4>
                  <h5>{description}</h5>
              </div>
          </div>
      </div>
  )
}

class TimeLine extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: 0, previous: 0 };
  }

  componentWillMount() {
    this.data = GameInfo.map((game, index) => {
      return ({
        date: game.date,
        component: (
          <React.Fragment>
            <div className='container' key={index}>
              <h1>{game.title}</h1>
              <h4>{ game.subtitle }</h4>
            </div>
            <div className='row funFactsRow athletic-stats-container'>
                {card(game.overallStats.value, game.overallStats.description, game.overallStats.iconType)}
                {card(game.ageStats.value, game.ageStats.description, game.ageStats.iconType)}
                {card(game.timeStats.value, game.timeStats.description, game.timeStats.iconType)}
                {card(game.paceStats.value, game.paceStats.description, game.paceStats.iconType)}
            </div>
          </React.Fragment>

        )
      });
    });
  }

  render() {
    return (
      <React.Fragment>
        <HorizontalTimelineContent content={this.data} />
        <div className='view-athlete-results' style={{textAlign: 'right', marginTop: 20}}><a href='https://www.athlinks.com/athletes/360791631/results' target="_blank" style={{color: '#008BDA'}}>View All Race Results</a></div>
      </React.Fragment>

    );
  }
}

export default TimeLine
